<style></style>
<template>
  <div>
    <h1>我是header{{ msg }}</h1>
    <button @click="PropParent">Click</button>
  </div>
</template>
<script>
  export default{
    props:['msg'],
    methods:{
      PropParent:function(){
        console.log('11')

        this.$emit('child-tell',this.msg)
        //另一种派发方式 已经被弃用
        //this.$dispatch('child-tell',this.msg)
      }
    }
  }
</script>



