<style>
 h1{
   color: red;
 }
</style>
<template>
  <div>
    <h1>{{ msg }}</h1>
    <h2 v-text="msg"></h2>
    <h3 v-html="msg"></h3>
    <router-link :to="{ name: 'HelloWorld', params: { id: 123 }}">User</router-link>
    <button @click="msg += 1"></button>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        msg: 'Hello Vue'
      }
    },
    watch:{
      msg: function (nval,oval) {
        console.log(nval)
        console.log(oval)
      }
    }
  }
</script>



