import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Defalut from '@/components/defalut'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component:Defalut
    },
    {
      path: '/hello/:id',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



